export const seats = [
  // Row A
  ...Array.from({ length: 10 }, (_, i) => ({
    id: `A${i + 1}`,
    label: `A${i + 1}`,
    row: 'A',
    status: i % 3 === 0 ? 'occupied' : i % 3 === 1 ? 'reserved' : 'available'
  })),

  // Row B
  ...Array.from({ length: 10 }, (_, i) => ({
    id: `B${i + 1}`,
    label: `B${i + 1}`,
    row: 'B',
    status: i % 3 === 1 ? 'occupied' : i % 3 === 2 ? 'reserved' : 'available'
  })),

  // Row C
  ...Array.from({ length: 10 }, (_, i) => ({
    id: `C${i + 1}`,
    label: `C${i + 1}`,
    row: 'C',
    status: i % 3 === 2 ? 'occupied' : i % 3 === 0 ? 'reserved' : 'available'
  }))
];
